.nav {
   margin-top: 40px;
   &__title {
      font-weight: 400;
      font-size: 17px;
      line-height: 28px;
      color: var(--text-grey);
      margin-bottom: 8px;
   }
   &__hashtags {
      display: flex;
      gap: 12px;
      &-tag {
         font-weight: 400;
         font-size: 17px;
         line-height: 28px;
         color: var(--text-grey);
         background-color: var(--text-grey-bg);
      }
   }
}